import { Component, OnInit } from '@angular/core';
import { MessageService } from 'primeng/api';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { OrderService } from '../services/order.service';

@Component({
  selector: 'app-address-info',
  templateUrl: './address-info.component.html',
  styleUrls: ['./address-info.component.scss'],
})
export class AddressInfoComponent implements OnInit {
  addressId: number;
  addressInfo
  constructor(private orderService: OrderService, public ref: DynamicDialogRef, public config: DynamicDialogConfig,) { }

  ngOnInit(): void {
    this.addressId = this.config.data.addressId
    this.getAddressInfo(this.addressId);
  }


  getAddressInfo(id) {
    this.orderService.getAddress(id).subscribe(data => {
      this.addressInfo = data;
    })
  }

}
