import { Component, OnInit } from '@angular/core';
import { MessageService, ConfirmationService } from 'primeng/api';
import { DialogService } from 'primeng/dynamicdialog';
import { forkJoin, Observable } from 'rxjs';
import { AddressInfoComponent } from '../address-info/address-info.component';
import { OrderService } from '../services/order.service'

@Component({
  selector: 'app-order-request',
  templateUrl: './order-request.component.html',
  styleUrls: ['./order-request.component.scss'],
  styles: [`
        :host ::ng-deep .p-dialog .product-image {
            width: 150px;
            margin: 0 auto 2rem auto;
            display: block;
        }
    `],
  providers: [MessageService, ConfirmationService, DialogService]
})
export class OrderRequestComponent implements OnInit {

  statuses = [
    { no: 1, name: 'Booked', icon: 'pi-calendar' },
    { no: 2, name: 'Shipped', icon: 'pi-cog' },
    { no: 3, name: 'Delivered', icon: 'pi-thumbs-up' },
    { no: 4, name: 'Canceled', icon: 'pi-user' },
  ]
  productDialog: boolean;
  selecteditems: string[];

  submitted: boolean;
  pageNo: number = 1;
  ListOrderCount: number;
  orderList = []

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    public dialogService: DialogService,
    private ordersService: OrderService,
  ) { }

  ngOnInit() {
    this.getListOrder();
  }



  getListOrder() {
    this.ordersService.getlistOrder({ pageNumber: this.pageNo }).subscribe(data => {
      this.orderList = data.data;
      this.ListOrderCount = data.count;
    })
  }

  loadMoreListOrdrData(event: any) {
    this.pageNo = event.page + 1;
    this.getListOrder();
  }

  deleteSelectedItems() {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete the selected Item?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const productsDeletedIDs: number[] = []
        let deleteRequests: Observable<any>[] = []

        this.orderList = this.orderList.filter(val => !this.selecteditems.includes(val));
        this.selecteditems.forEach(item => {
          deleteRequests.push(this.ordersService.deleteOrderRequest(item['id']))
          productsDeletedIDs.push(item['id'])
        })
        this.selecteditems = [];
        forkJoin(deleteRequests)
          .subscribe((res) => {
            this.messageService.add({ severity: 'success', summary: 'Successful', detail: `Order with Id/s ( ${productsDeletedIDs.toString()} ) Successfully Deleted`, life: 3000 });
          })
      }
    })
  }

  changeProductStatus(id: number, statusNo: number,) {
    this.ordersService.changeOrderRequestStatus(id, statusNo)
      .subscribe(
        (res) => {
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: `order #${id} status successfully changed`, life: 3000 });
          this.getListOrder()
        }
      )
  }


  deleteItem(item) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete request Number' + item.requestNumber + '?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.ordersService.deleteOrderRequest(item.id).subscribe(data => {
          if (data.succeeded) {
            this.messageService.add({ severity: 'success', summary: 'Successful', detail: `Order with Id #${item.id} Successfully Deleted`, life: 2000 });
            this.getListOrder()
            this.removeItemFromSelectedItems(item.id)
          }
        })
      }
    });
  }

  // refresh the selected items if the deleted item is already selected
  removeItemFromSelectedItems(id: number) {
    const filteredItems = this.selecteditems.filter((_item: any) => {
      return _item.id != id
    })
    this.selecteditems = [...filteredItems]
  }

  getAddress(addressId) {
    // 
    // console.log(typeOfAddress, 'typeOfAddress')
    const ref = this.dialogService.open(AddressInfoComponent, {
      data: {
        addressId,
      },
      header: `Address Info`,
      width: '30%',
      contentStyle: { "max-height": "500px", "overflow": "auto" },
    });

  }

}
