import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  prefix: string = "Order"
  get endPoint(): string { return `${environment.api}/${this.prefix}` }
  constructor(private http: HttpClient) { }


  getlistOrder({ pageNumber = 1, pageSize = 10 }) {
    return this.http.get<ApiResponse>(`${this.endPoint}/?PageNumber=${pageNumber}&PageSize=${pageSize}`)
  }

  changeOrderRequestStatus(orderId: number, statusNo: number) {
    let reqBody = {
      id: orderId,
      status: statusNo
    }
    return this.http.put<ApiResponse<number>>(`${this.endPoint}/updateStatus-${orderId}`, reqBody)
  }

  deleteOrderRequest(id: number) {
    return this.http.delete<ApiResponse<number>>(`${this.endPoint}/${id}`)
  }

  getAddress(id: number) {
    return this.http.get<ApiResponse<number>>(`${environment.api}/Address/${id}`).pipe(map(res => {
      return res.data
    }))
  }
}
